 <a  @click=\"goBack\" href=\"#\" class=\"btn btn-outline-primary px-5 me-2\">Cancel</a>\n                                    <button :disabled=\"!canSubmitCashIn\" type=\"submit\" v-html=\"cashInBtnState\"\n                                        class=\"btn btn-primary px-5\" style=\"background-color: #041b3f;\">\n                                    </button>\n                                </div>\n                            </form>\n                        </div>\n                    </div>\n                </div>\n                <div class=\"tab-pane fade\" id=\"pills-profile\" role=\"tabpanel\" aria-labelledby=\"profile-tab\">\n                    <div class=\"tab-pane fade show active\" id=\"pills-home\" role=\"tabpanel\" aria-labelledby=\"home-tab\">\n                        <div class=\"card w-100\" style=\"border-radius: 10px;\">\n                            <div class=\"card-header text-white\"\n                                style=\"background-color: #041b3f; border-top-left-radius: 10px; border-top-right-radius: 10px;\">\n                                <h5 class=\"mb-0 text-white\">CASH OUT</h5>\n                            </div>\n                            <div class=\"card-body\">\n                                <form @submit.prevent=\"cashOut\" class=\"px-4\">\n\n                                    <div class=\"mb-3\">\n                                        <label for=\"wallet_number\" class=\"form-label\">Phone No or Ac no</label>\n                                        <input type=\"text\" class=\"form-control\" id=\"account_no\" name=\"walletId\"\n                                            placeholder=\"Enter Phone No or Ac no\" v-model=\"cashOutForm.wallet_number\">\n                                        <div v-if=\"cashOutIsValidating\" class=\"spinner mt-2\"></div>\n                                    </div>\n\n                                    <!-- Customer's Name -->\n                                    <div v-if=\"cash_out_is_verified\" class=\"mb-3\">\n                                        <label for=\"customer_name\" class=\"form-label\">Recipient Name</label>\n                                        <input disabled type=\"text\" class=\"form-control\" id=\"customer_name\"\n                                            placeholder=\"Enter customer name\" v-model=\"cashOutForm.recipientName\">\n                                    </div>\n                                    <!-- Amount -->\n                                    <div v-if=\"cash_out_is_verified\" class=\"mb-3\">\n                                        <label for=\"cash-in-amount\" class=\"form-label\">Amount</label>\n                                        <input type=\"text\" class=\"form-control\" id=\"cash-in-amount\"\n                                            placeholder=\"Enter Amount\" v-model=\"cashOutForm.amount\">\n                                    </div>\n\n                                    <div class=\"d-flex justify-content-end\">\n                                        <a @click=\"goBack\" href=\"#\" class=\"btn btn-outline-primary px-5 me-2\">Cancel</a>\n                                        <button :disabled=\"!canSubmitCashOut\" type=\"submit\" v-html=\"cashOutBtnState\"\n                                            class=\"btn btn-primary px-5\" style=\"background-color: #041b3f;\">\n                                        </button>\n                                    </div>\n                                </form>\n                            </div>\n                        </div>\n                    </div>\n\n                </div>\n                <!--modal-->\n\n                <a style=\"visibility: hidden\" id=\"modalBtn\" href=\"#\" class=\"btn\" data-bs-toggle=\"modal\"\n                    data-bs-target=\"#modal-success\">\n                    Success modal\n                </a>\n                <!-- Vertically centered scrollable modal -->\n                <modal :message=\"modal.message\" :title=\"modal.title\" :type=\"modal.type\"></modal>\n            </div>\n        </div>\n\n    </div>\n</template>\n<script>\nimport Form from '@utils/form/Form'\nimport Modal from \"./modal/Modal.vue\";\n\nexport default {\n    props: {\n        routes: {\n            type: Object,\n            required: true,\n            default: () => { }\n        },\n        page_info: {\n            type: Object,\n            required: true,\n            default: () => { }\n        }\n    },\n    components: { Modal },\n    data() {\n        return {\n            cashInIsValidating: false,\n            cash_in_is_verified: false,\n            cashInBtnState: 'Submit',\n            cashInForm: new Form({\n                wallet_number: '',\n                walletId: '',\n                is_verified: false,\n                recipientName: '',\n                groupId: '',\n                amount: '',\n            }),\n            cashOutIsValidating: false,\n            cash_out_is_verified: false,\n            cashOutBtnState: 'Submit',\n            cashOutForm: new Form({\n                wallet_number: '',\n                walletId: '',\n                is_verified: false,\n                recipientName: '',\n                groupId: '',\n                amount: '',\n            }),\n            modal: {\n                message: 'Make payment',\n                type: 'failed',\n                title: 'Error'\n            }\n\n        };\n    },\n    mounted(){\n        if(location.hash == '#cash-out'){\n            document.querySelector(\"#pills-profile-tab\").click()\n        }\n    },\n    computed: {\n        canSubmitCashIn() {\n            return (\n                this.cashInForm.amount !== '' && this.cashInForm.processing != true\n            );\n        },\n        canSubmitCashOut() {\n            return (\n                this.cashOutForm.amount !== '' && this.cashOutForm.processing != true\n            );\n        },\n    },\n    watch: {\n        'cashInForm.wallet_number': function (value) {\n            if (this.typingTimeout) clearTimeout(this.typingTimeout);\n\n            if (value.length > 3 && this.cashInForm.is_verified === false) {\n                this.typingTimeout = setTimeout(() => {\n                    this.cashInIsValidating = true\n                    this.cashInForm.post(`${this.page_info?.domain}bank/verify-wallet-id`)\n                        .then((data) => {\n                            if (data.status === \"SUCCESS\") {\n                                this.cashInIsValidating = false;\n                                this.cash_in_is_verified = true;\n                                this.cashInForm.walletId = data.walletId;\n                                this.cashInForm.groupId = data.groupId;\n                                this.cashInForm.is_verified = true;\n                                this.cashInForm.recipientName = data.walletName;\n                                this.cashInForm.wallet_number = data.walletId;\n                            } else {\n                                this.cash_in_is_verified = false;\n                                this.cashInForm.is_verified = false;\n                                this.cashInIsValidating = false;\n                                this.modal.type = 'failed',\n                                    this.modal.message = data.message,\n                                    this.modal.title = 'Not Found'\n                                let bn = document.getElementById('modalBtn');\n                                setTimeout(() => bn.click(), 200)\n                            }\n                        })\n                        .catch((error) => {\n\n                            this.cashInIsValidating = false;\n\n                            if (error.response && error.response.data && error.response.data.errors) {\n                                this.cashInForm.errors.record(error.response.data.errors);\n                            } else {\n                                this.$toast.error(error.response.data);\n                            }\n                        });\n                }, 1500);\n            }\n        },\n        'cashOutForm.wallet_number': function (value) {\n            if (this.typingTimeout) clearTimeout(this.typingTimeout);\n\n            if (value.length > 3 && this.cashOutForm.is_verified === false) {\n                this.typingTimeout = setTimeout(() => {\n                    this.cashOutIsValidating = true\n                    this.cashOutForm.post(`${this.page_info?.domain}bank/verify-wallet-id`)\n                        .then((data) => {\n                            if (data.status === \"SUCCESS\") {\n                                this.cashOutIsValidating = false;\n                                this.cash_out_is_verified = true;\n                                this.cashOutForm.walletId = data.walletId;\n                                this.cashOutForm.groupId = data.groupId;\n                                this.cashOutForm.is_verified = true;\n                                this.cashOutForm.recipientName = data.walletName;\n                                this.cashOutForm.wallet_number = data.walletId;\n                            } else {\n                                this.cash_out_is_verified = false;\n                                this.cashOutForm.is_verified = false;\n                                this.cashOutIsValidating = false;\n                                this.modal.type = 'failed',\n                                    this.modal.message = data.message,\n                                    this.modal.title = 'Not Found'\n                                let bn = document.getElementById('modalBtn');\n                                setTimeout(() => bn.click(), 200)\n                            }\n                        })\n                        .catch((error) => {\n                            this.cashOutIsValidating = false;\n                            if (error.response && error.response.data && error.response.data.errors) {\n                                this.cashInForm.errors.record(error.response.data.errors);\n                            } else {\n                                this.$toast.error(error.response.data);\n                            }\n                        });\n                }, 1500);\n            }\n        }\n    },\n    methods: {\n        goBack() {\n            window.history.back();\n        },\n        cashIn() {\n            this.cashInBtnState = `Submitting...`\n            this.cashInForm.post(this.routes.cash_in_route)\n                .then((data) => {\n                    if (data.status === \"SUCCESS\") {\n                        this.modal.type = 'success',\n                            this.modal.message = \"Your cash-in payment of has been successfully submitted.\"\n                        this.modal.title = 'Payment succeeded'\n                        this.cash_in_is_verified = false;\n                        this.cashInForm.is_verified = false;\n                        this.cashInBtnState = `Submit`\n                        let bn = document.getElementById('modalBtn');\n                        setTimeout(() => bn.click(), 200)\n                    } else {\n                        this.modal.type = 'failed',\n                            this.modal.message = data.message,\n                            this.modal.title = 'Payment not succeeded'\n                        this.cash_in_is_verified = false;\n                        this.cashInForm.is_verified = false;\n                        this.cashInIsValidating = false;\n                        this.cashInBtnState = `Submit`\n                        let bn = document.getElementById('modalBtn');\n                        setTimeout(() => bn.click(), 200)\n                    }\n                })\n                .catch((error) => {\n                    this.response = true\n                    this.responseMessage = \"An error occurred when connecting\";\n                    this.cashInIsValidating = false;\n                    this.cash_in_is_verified = true;\n                    this.cashInForm.is_verified = true;\n                    this.cashInBtnState = `Submit`\n                });\n        },\n        cashOut() {\n            this.cashOutBtnState = `Submitting...`\n            this.cashOutForm.post(this.routes.cash_out_route)\n                .then((data) => {\n                    if (data.status === \"SUCCESS\") {\n                        this.modal.type = 'success',\n                            this.modal.message = \"Your cash-out payment of has been successfully submitted.\"\n                        this.modal.title = 'Payment succeeded'\n                        this.cash_out_is_verified = false;\n                        this.cashOutForm.is_verified = false;\n                        this.cashOutBtnState = `Submit`\n                        let bn = document.getElementById('modalBtn');\n                        setTimeout(() => bn.click(), 200)\n                    } else {\n                        this.modal.type = 'failed',\n                            this.modal.message = data.message,\n                            this.modal.title = 'Payment not succeeded'\n                        this.cash_out_is_verified = false;\n                        this.cashOutForm.is_verified = false;\n                        this.cashInIsValidating = false;\n                        this.cashOutBtnState = `Submit`\n                        let bn = document.getElementById('modalBtn');\n                        setTimeout(() => bn.click(), 200)\n                    }\n                })\n                .catch((error) => {\n                    this.response = true\n                    this.responseMessage = \"An error occurred when connecting\";\n                    this.cashInIsValidating = false;\n                    this.cash_out_is_verified = true;\n                    this.cashOutForm.is_verified = true;\n                    this.cashOutBtnState = `Submit`\n                });\n        },\n    },\n};\n</script>\n<style scoped>\n.spinner {\n    display: inline-block;\n    width: 20px;\n    height: 20px;\n    border: 3px solid rgba(0, 0, 0, 0.1);\n    border-left-color: #041b3f;\n    border-radius: 50%;\n    animation: spin 1s linear infinite;\n}\n\n@keyframes spin {\n    100% {\n        transform: rotate(360deg);\n    }\n}\n</style>\n"],"sourceRoot":""}]);
