                        <button type="button" class="btn btn-primary">Upload</button>
                    </div>
                </div>
            </div>
        </div>
        <div class="modal fade" id="change-pin" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <h1 class="modal-title fs-5" id="exampleModalLabel">Upload image</h1>
                        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div class="modal-body">
                        <input  ref="fileInput" type="file" @change="handleFileUpload" />
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                        <button type="button" class="btn btn-primary">Upload</button>
                    </div>
                </div>
            </div>
        </div>
        <div class="row">

            <div class="col-lg-6 mx-auto">
                <div class="card w-100 " style="margin-top: 50px; border-radius: 10px;">
                    <div class="card-header text-white" style="background-color: #041b3f; border-top-left-radius: 10px; border-top-right-radius: 10px;">

                    </div>
                    <div class="card-body">
                        <div class="px-5">
                            <div class="d-flex flex-column justify-content-center align-items-center">
                                <div style="width: 200px;" class="profile-pic position-relative">
                                    <img style="width: 100%; border-radius:50%" src="/assets/images/users/avatar-1.jpg" alt="">
                                    <div  data-bs-toggle="modal" data-bs-target="#profile-image-upload" class="position-absolute"><div class="text-center"><div class="mx-auto"><i class="bi bi-camera fs-36"></i></div> <p> Change profile image</p></div>   </div>
                                </div>
                                <div class="fw-bold my-2">{{ user.tellerName }}</div>
                                <div class="mb-4">{{ user.email }}</div>
                            </div>
                            <ul class="nav nav-pills mb-4" id="pills-tab" role="tablist">
                                <li class="nav-item" role="presentation">
                                    <button style="margin-right:1rem" class="nav-link p-2 active" id="pills-home-tab"
                                            data-bs-toggle="pill" data-bs-target="#pills-home" type="button" role="tab"
                                            aria-controls="pills-home" aria-selected="true">Basic Info
                                    </button>
                                </li>
                                <li class="nav-item" role="presentation">
                                    <button style="" class="nav-link" id="pills-profile-tab" data-bs-toggle="pill"
                                            data-bs-target="#pills-profile" type="button" role="tab" aria-controls="pills-profile"
                                            aria-selected="false">Change PIN
                                    </button>
                                </li>
                            </ul>
                            <div class="tab-content" id="myTabContent">
                                <div class="tab-pane fade show active" id="pills-home" role="tabpanel" aria-labelledby="home-tab">
                                    <div class="w-100">
                                        <div class="card-header text-white"
                                             style="background-color: rgba(241,241,241,0.61);border-radius: 0">
                                            <h6 class="mb-0">Basic Info</h6>
                                        </div>
                                        <div class="card-body">
                                            <form @submit.prevent="updateProfile" id="walletForm">
                                                <div class="mb-3">
                                                    <label for="narration" class="form-label">Name</label>
                                                    <input :disabled="role.name === 'teller'" type="text"  v-model="profile.tellerName" class="form-control" id="project" placeholder="Enter Phone">
                                                </div>
                                                <div class="mb-3">
                                                    <label for="narration" class="form-label">Phone Number</label>
                                                    <input :disabled="role.name === 'teller'" type="text"  v-model="profile.tellerPhone" class="form-control" id="project" placeholder="Enter Phone">
                                                </div>
                                                <div class="mb-3">
                                                    <label for="narration" class="form-label">Email Address</label>
                                                    <input :disabled="role.name === 'teller'" type="email" class="form-control" v-model="profile.email" id="project" placeholder="Enter Email Address">
                                                </div>
                                                <div class="d-flex gap-3 flex-wrap flex-column-reverse flex-md-row justify-content-md-end">
                                                    <button @click="goBack" type="button" class="btn btn-outline-primary px-5">Cancel</button>
                                                    <button :disabled="canUpdate" type="submit" class="btn btn-primary px-5" style="background-color: #041b3f;">{{ stateBtn }}</button>
                                                </div>
                                            </form>
                                        </div>
                                    </div>
                                </div>
                                <div class="tab-pane fade" id="pills-profile" role="tabpanel" aria-labelledby="profile-tab">
                                    <div class="w-100" >
                                        <div  class="card-header text-white"
                                             style="background-color: #f1f1f1;border-radius: 0">
                                            <h6 class="mb-0">Change PIN</h6>
                                        </div>
                                        <div class="card-body">
                                            <form class="mx-auto" @submit.prevent="verifyPin" method="POST" id="walletForm">
                                                <div class="mb-1">
                                                    <div v-if="response">
                                                        <div :class="`alert ${responseType}`">
                                                            {{ responseMessage }}
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="mb-4  d-flex flex-column justify-content-center">
                                                    <label for="new-pin" class="form-label d-block text-start">New PIN</label>
                                                    <div class="d-flex align-items-center gap-4">
                                                        <input v-for="(pin, index) in newPinInputs" :key="`new-pin-${index}`"
                                                               ref="newPinInputs" type="password" class="form-control text-center otp-input"
                                                               id="pin"
                                                               maxlength="1" v-model="newPinInputs[index]"
                                                               @input="handleInput(index, 'new')"
                                                               @keydown="handleKeyDown($event, index, 'new')"
                                                               @paste="handlePaste($event, 'new')" @focus="handleFocus" />
                                                        <div @click="handleShowPin"><i :class="`bi ${showPin ? 'bi-eye' : 'bi-eye-slash'} fs-22`"></i></div>
                                                    </div>
                                                </div>

                                                <div class="mb-4">
                                                    <label for="confirm-pin" class="form-label d-block text-start">Confirm PIN</label>
                                                    <div class="d-flex gap-4 align-items-center">
                                                        <input v-for="(pin, index) in confirmPinInputs" :key="`confirm-pin-${index}`"
                                                               ref="confirmPinInputs" type="password"
                                                               id="confirmPin"
                                                               class="form-control text-center otp-input" maxlength="1"
                                                               v-model="confirmPinInputs[index]" @input="handleInput(index, 'confirm')"
                                                               @keydown="handleKeyDown($event, index, 'confirm')"
                                                               @paste="handlePaste($event, 'confirm')" @focus="handleFocus" />
                                                        <div @click="handleShowConfirmPin" ><i :class="`bi ${showConfirmPin ? 'bi-eye' : 'bi-eye-slash'} fs-22`"></i></div>
                                                    </div>
                                                </div>

                                                <div class="d-flex gap-3 flex-wrap flex-column-reverse flex-md-row justify-content-md-end">
                                                    <button @click="goBack" type="button" class="btn btn-outline-primary px-5">Cancel</button>
                                                    <button :disabled="!canSubmit" v-html="state" type="submit" class="btn btn-primary px-5"
                                                            style="background-color: #041b3f;"></button>
                                                </div>
                                            </form>
                                        </div>
                                    </div>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
                <a style="visibility: hidden" id="modalBtn" href="#" class="btn" data-bs-toggle="modal"
                        data-bs-target="#modal-success">
                        Success modal
                    </a>
                    <!-- Vertically centered scrollable modal -->
                    <modal :message="modal.message" :title="modal.title" :type="modal.type"></modal>
            </div>

        </div>
    </div>

    </template>
    <script>
    import Modal from "../payments/modal/Modal.vue";
    import Form from '@utils/form/Form'

    export default {
        components:{
            Modal
        },
        props:{
            user: {
                type: Object,
                required: true,
                default: () => {
                },
            },
            page_info:{
                type: Object
            },
            role:{
                type: Object,
                required: false,
                default: () => {

                }
            }
        },
        data() {
            return {
                selectedFile: null,
                previewImage: null,
                walletId: '',
                customerName: '',
                profile: new Form({
                    tellerName: '',
                    tellerPhone: '',
                    email: ''
                }, {
                    resetOnSuccess: false,
                }),
                form: new Form({
                    wallet_number: '',
                    walletId: '',
                    is_verified: false,
                    customerName: '',
                    newPin: ''
                }),
                modal: {
                    message: 'Unexpected Error Occurred',
                    type: 'failed',
                    title: 'Error'
                },
                response: false,
                responseType: 'alert-danger',
                responseMessage: 'Error',
                showConfirmPin: false,
                showPin: false,
                hidePin:'bi-eye-slash',
                is_verified: false,
                state: 'Submit',
                stateBtn: 'Submit',
                newPinInputs: Array(4).fill(""),
                confirmPinInputs: Array(4).fill(""),
                isValidating: false,
                typingTimeout: null,
            };
        },

        computed: {
            canUpdate(){
                return(
                    this.profile.processing
                );
            },
            canSubmit() {
                return (
                    this.newPinInputs.join('').length === 4 &&
                    this.confirmPinInputs.join('').length === 4 &&
                    this.newPinInputs.join('') === this.confirmPinInputs.join('')
                    && this.form.processing === false
                );
            },
        },
        created() {
            this.profile.populate(this.user);
        },
        methods: {
            updateProfile() {
                this.stateBtn = `Updating...`
                this.canSubmit = true;
                this.profile.post(`${this.page_info?.domain}bank/update-profile`)
                    .then((data) => {
                        if (data.status === "SUCCESS") {
                            this.is_verified = false;
                            this.modal.type = 'success';
                            let message = '';
                            let failed_message = '';
                            data.updatedFields.forEach((field)=> message += `${field}, `);
                            data.failedFields.forEach((field)=> failed_message += `${field}, `);
                            console.log(message)
                            this.modal.message = `${message} ${message ? 'Updated' : 'nothing to Updated'}. ${failed_message} ${failed_message && 'failed'}`
                            data.updatedFields.length !== 0 ? this.modal.title = 'Profile Updated' : this.modal.title = 'Profile Updated Failed'
                            this.stateBtn = `Updated`
                            let bn = document.getElementById('modalBtn');
                            setTimeout(() => bn.click(), 200)
                            message && setTimeout(()=>window.location.reload(),1000)
                        } else {
                            this.response = true
                            this.responseMessage = data.message;
                            this.is_verified = false;
                            this.form.is_verified = false;
                            this.isValidating = false;
                            this.stateBtn = `Update`
                            this.modal.type = 'failed',
                            this.modal.message = "Your payment of has been successfully submitted."
                            this.modal.title = 'Payment succeeded'
                            let bn = document.getElementById('modalBtn');
                            setTimeout(() => bn.click(), 200)

                        }
                    })
                    .catch((error) => {
                        this.response = true
                        this.responseMessage = "An error occurred when connecting";
                        this.isValidating = false;
                        this.is_verified = true;
                        this.form.is_verified = true;
                        this.stateBtn = `Update`
                        this.modal.type = 'failed',
                            this.modal.message = "Nothing Updated"
                            this.modal.title = 'Profile Updated Failed'
                            let bn = document.getElementById('modalBtn');
                            setTimeout(() => bn.click(), 200)

                    });
            },
            goBack() {
                window.history.back();
            },
            handleFileUpload(event) {
                this.selectedFile = null;
                this.previewImage = null;

                let file = event.target.files[0];

                if (file) {
                    this.selectedFile = file;

                    let reader = new FileReader();
                    reader.onload = (e) => {
                        this.previewImage = e.target.result;
                    };
                    reader.readAsDataURL(file);

                }
            },
            handleShowConfirmPin(){
                this.showConfirmPin = !this.showConfirmPin
                let input = document.querySelectorAll('#confirmPin')
                input.forEach((input)=>input.type = this.showConfirmPin ? 'text' : 'password')
            },
            handleShowPin(){
                this.showPin = !this.showPin
                let input = document.querySelectorAll('#pin')
                input.forEach((input)=>input.type = this.showPin ? 'text' : 'password')
            },
            handleInput(index, type) {
                const pinArray = type === 'new' ? this.newPinInputs : this.confirmPinInputs;
                if (pinArray[index] !== "" && index < pinArray.length - 1) {
                    this.$refs[`${type}PinInputs`][index + 1].focus();
                }
            },
            handleKeyDown(event, index, type) {
                const pinArray = type === 'new' ? this.newPinInputs : this.confirmPinInputs;
                if (event.key === 'Backspace' && pinArray[index] === '' && index > 0) {
                    this.$refs[`${type}PinInputs`][index - 1].focus();
                }
            },
            handleFocus(event) {
                event.target.select();
            },
            handlePaste(event, type) {
                event.preventDefault();
                const text = event.clipboardData.getData('text').replace(/\D/g, '');
                text.split('').slice(0, 4).forEach((digit, index) => {
                    this[`${type}PinInputs`][index] = digit;
                    this.$refs[`${type}PinInputs`][index].focus();
                });
            },
            verifyPin() {
                this.form.newPin = this.newPinInputs.join('');
                this.state = `Resetting...`
                this.canSubmit = true;
                this.form.post(`${this.page_info?.domain}profile/change-pin`)
                    .then((data) => {
                        if (data.status === "SUCCESS") {
                            this.response = true
                            this.responseType = 'alert-success'
                            this.responseMessage = data.message;
                            this.is_verified = false;
                            this.form.is_verified = false;
                            this.newPinInputs= Array(4).fill(""),
                            this.confirmPinInputs= Array(4).fill(""),
                            this.state = `Submit`
                            this.form.reset()
                            setTimeout(()=> document.querySelector('#pills-home-tab').click(),1000)


                        } else {
                            this.response = true
                            this.responseMessage = data.message;
                            this.is_verified = false;
                            this.form.is_verified = false;
                            this.isValidating = false;
                            this.state = `Submit`

                        }
                    })
                    .catch((error) => {
                        this.response = true
                        this.responseMessage = "An error occurred when connecting";
                        this.isValidating = false;
                        this.is_verified = true;
                        this.form.is_verified = true;
                        this.state = `Submit`
                    });
            },
        },
    };
    </script>
