<template>

    <div class="row">
        <div class="col-lg-6 me-auto">
            <div class="card w-100 mt-5" style="border-radius: 10px;">
                <div class="card-header " style="background-color: #041b3f; border-radius: 10px 10px 0 0;">
                    <!-- <h5 class="mb-0 text-white">Reset PIN</h5> -->
                </div>

                <div class="card-body">
                    <form class="px-lg-3" @submit.prevent="verifyPin">
                        <!-- Phone No or Account No -->
                        <div class="mb-3">
                            <label for="account_no" class="form-label">Phone No or Ac no</label>
                            <input :disabled="is_verified" type="text" class="form-control" id="account_no" name="walletId"
                                placeholder="Enter Phone No or Ac no" v-model="form.wallet_number">
                            <div v-if="isValidating" class="spinner mt-2"></div>
                        </div>

                        <!-- Customer's Name -->
                        <div v-if="is_verified" class="mb-3">
                            <label for="customer_name" class="form-label">Customer's Name</label>
                            <input :disabled="is_verified" type="text" class="form-control" id="customer_name"
                                placeholder="Enter customer name" v-model="form.customerName">
                        </div>

                        <div v-if="is_verified" class="mt-6">
                            <p class="text-muted">Provide the new pin for the customer</p>
                            <div class="mb-4">
                                <label for="new-pin" class="form-label d-block text-start">New PIN</label>
                                <div class="d-flex align-items-center gap-4">
                                    <input v-for="(pin, index) in newPinInputs" :key="`new-pin-${index}`"
                                        ref="newPinInputs" type="password" class="form-control text-center otp-input"
                                           id="pin"
                                        maxlength="1" v-model="newPinInputs[index]"
                                        @input="handleInput(index, 'new')"
                                        @keydown="handleKeyDown($event, index, 'new')"
                                        @paste="handlePaste($event, 'new')" @focus="handleFocus" />
                                    <div @click="handleShowPin"><i :class="`bi ${showPin ? 'bi-eye' : 'bi-eye-slash'} fs-22`"></i></div>
                                </div>
                            </div>

                            <div class="mb-4">
                                <label for="confirm-pin" class="form-label d-block text-start">Confirm PIN</label>
                                <div class="d-flex gap-4 align-items-center">
                                    <input v-for="(pin, index) in confirmPinInputs" :key="`confirm-pin-${index}`"
                                        ref="confirmPinInputs" type="password"
                                           id="confirmPin"
                                        class="form-control text-center otp-input" maxlength="1"
                                        v-model="confirmPinInputs[index]" @input="handleInput(index, 'confirm')"
                                        @keydown="handleKeyDown($event, index, 'confirm')"
                                        @paste="handlePaste($event, 'confirm')" @focus="handleFocus" />
                                    <div @click="handleShowConfirmPin" ><i :class="`bi ${showConfirmPin ? 'bi-eye' : 'bi-eye-slash'} fs-22`"></i></div>
                                </div>
                            </div>
                        </div>


                        <div class="d-flex gap-3 flex-wrap flex-column-reverse flex-md-row justify-content-md-end">
                            <button @click="goBack" type="button" class="btn btn-outline-primary px-5">Cancel</button>
                            <button :disabled="!canSubmit" v-html="state" type="submit" class="btn btn-primary px-5"
                                style="background-color: #041b3f;"></button>
                        </div>
                    </form>
                </div>
                <a style="visibility: hidden" id="modalBtn" href="#" class="btn" data-bs-toggle="modal"
                data-bs-target="#modal-success">
                Success modal
            </a>
            <!-- Vertically centered scrollable modal -->
            <modal :message="modal.message" :title="modal.title" :type="modal.type"></modal>
            </div>
        </div>

    </div>

</template>

<script>
import Form from '@utils/form/Form'
import Modal from '../payments/modal/Modal.vue';
export default {
components: { Modal },
props: {
    page_info:{
        type:Object
    }
},
data() {
    return {
        walletId: '',
        customerName: '',
        form: new Form({
            wallet_number: '',
            walletId: '',
            is_verified: false,
            customerName: '',
            newPin: ''
        }, {
            resetOnSuccess: false,
        }),
        modal: {
            message: 'Unexpected Error Occurred',
            type: 'failed',
            title: 'Error'
        },
        showConfirmPin: false,
        showPin: false,
        hidePin:'bi-eye-slash',
        is_verified: false,
        state: 'Submit',
        newPinInputs: Array(4).fill(""),
        confirmPinInputs: Array(4).fill(""),
        isValidating: false,
        typingTimeout: null,  // Add a timeout reference
    };
},
watch: {
    'form.wallet_number': function (value) {
        if (this.typingTimeout) clearTimeout(this.typingTimeout);

        if (value.length > 3 && this.form.is_verified === false) {
            this.typingTimeout = setTimeout(() => {
                this.isValidating = true
                this.form.post(`${this.page_info?.domain}bank/verify-wallet-id`)
                    .then((data) => {
                        if (data.status === "SUCCESS") {
                            this.isValidating = false;
                            this.is_verified = true;
                            this.form.walletId = data.walletId;
                            this.form.is_verified = true;
                            this.form.customerName = data.walletName;
                            this.form.wallet_number = data.walletId;
                        } else {
                            this.is_verified = false;
                            this.form.is_verified = false;
                            this.isValidating = false;
                            this.modal.type = 'failed',
                            this.modal.message = data.message,
                            this.modal.title = 'Not Found'
                            let bn = document.getElementById('modalBtn');
                            setTimeout(() => bn.click(), 200)
                        }
                    })
                    .catch((error) => {
                        this.isValidating = false;
                        if (error.response && error.response.data && error.response.data.errors) {
                            this.form.errors.record(error.response.data.errors);
                        } else {
                            this.$toast.error(error.response.data);
                        }
                    });
            }, 1500);
        }
    }
},
computed: {
    canSubmit() {
        return (
            this.newPinInputs.join('').length === 4 &&
            this.confirmPinInputs.join('').length === 4 &&
            this.newPinInputs.join('') === this.confirmPinInputs.join('')
            && this.form.processing === false && this.is_verified !== false
        );
    },
},
methods: {
    handleShowConfirmPin(){
        this.showConfirmPin = !this.showConfirmPin
        let input = document.querySelectorAll('#confirmPin')
        input.forEach((input)=>input.type = this.showConfirmPin ? 'text' : 'password')
    },
    handleShowPin(){
        this.showPin = !this.showPin
        let input = document.querySelectorAll('#pin')
        input.forEach((input)=>input.type = this.showPin ? 'text' : 'password')
    },
    handleInput(index, type) {
        const pinArray = type === 'new' ? this.newPinInputs : this.confirmPinInputs;
        if (pinArray[index] !== "" && index < pinArray.length - 1) {
            this.$refs[`${type}PinInputs`][index + 1].focus();
        }
    },
    handleKeyDown(event, index, type) {
        const pinArray = type === 'new' ? this.newPinInputs : this.confirmPinInputs;
        if (event.key === 'Backspace' && pinArray[index] === '' && index > 0) {
            this.$refs[`${type}PinInputs`][index - 1].focus();
        }
    },
    handleFocus(event) {
        event.target.select();
    },
    handlePaste(event, type) {
        event.preventDefault();
        const text = event.clipboardData.getData('text').replace(/\D/g, ''); // Allow only numeric
        text.split('').slice(0, 4).forEach((digit, index) => {
            this[`${type}PinInputs`][index] = digit;
            this.$refs[`${type}PinInputs`][index].focus();
        });
    },
    goBack() {
        window.history.back();
    },
    verifyPin() {
        this.form.newPin = this.newPinInputs.join('');
        this.state = `Resetting...`
        this.canSubmit = true;
        this.form.post(`${this.page_info?.domain}bank/reset-customer-pin`)
            .then((data) => {
                if (data.status === "SUCCESS") {

                    this.modal.type = 'success',
                    this.modal.message = data.message,
                    this.modal.title = 'PIN Reset succeeded'
                    let bn = document.getElementById('modalBtn');
                    setTimeout(() => bn.click(), 200)
                    this.is_verified = false;
                    this.form.is_verified = false;
                    this.form.reset()
                    this.state = `Submit`
                } else {
                    this.is_verified = true;
                    this.form.is_verified = true;
                    this.isValidating = false;
                    this.state = `Submit`
                    this.modal.type = 'failed',
                    this.modal.message = data.message,
                    this.modal.title = 'PIN Reset Failed'
                    let bn = document.getElementById('modalBtn');
                    setTimeout(() => bn.click(), 200)

                }
            })
            .catch((error) => {
                this.response = true
                this.responseMessage = "An error occurred when connecting";
                this.isValidating = false;
                this.is_verified = true;
                this.form.is_verified = true;
                this.state = `Submit`
            });
    },
},
};
</script>


<style scoped>
.otp-input {
width: 60px;
height: 60px;
font-size: 2rem;
font-weight: bold;
}

.form-label {
font-weight: bold;
}

.spinner {
display: inline-block;
width: 20px;
height: 20px;
border: 3px solid rgba(0, 0, 0, 0.1);
border-left-color: #041b3f;
border-radius: 50%;
animation: spin 1s linear infinite;
}

@keyframes spin {
100% {
    transform: rotate(360deg);
}
}
</style>
