___CSS_LOADER_EXPORT___.push([module.id, "\n.loader[data-v-07a65f7e] {\n  border: 8px solid #f3f3f3; /* Light grey */\n  border-top: 8px solid #3498db; /* Blue */\n  border-radius: 50%;\n  width: 60px;\n  height: 60px;\n  animation: spin-07a65f7e 1s linear infinite;\n}\n@keyframes spin-07a65f7e {\n0% { transform: rotate(0deg);\n}\n100% { transform: rotate(360deg);\n}\n}\n\n", "",{"version":3,"sources":["webpack://./resources/js/components/pages/tellerDashboard/charts/TransactionsChart.vue"],"names":[],"mappings":";AAyGA;EACA,yBAAA,EAAA,eAAA;EACA,6BAAA,EAAA,SAAA;EACA,kBAAA;EACA,WAAA;EACA,YAAA;EACA,2CAAA;AACA;AAEA;AACA,KAAA,uBAAA;AAAA;AACA,OAAA,yBAAA;AAAA;AACA","sourcesContent":["<template>\n    <div>\n        <apexchart v-if=\"loaded\" type=\"line\" height=\"350\" :options=\"chartOptions\" :series=\"series\"></apexchart>\n\n        <div v-else class=\"loader mx-auto\"></div>\n\n    </div>\n</template>\n\n<script>\n   import VueApexCharts from 'vue-apexcharts';\nimport axios from 'axios';\n\nexport default {\n    props:{\n        page_info:{\n            type: Object\n        }\n    },\n    components: {\n        apexchart: VueApexCharts,\n    },\n    data() {\n        return {\n            series: [],\n            xAxisLabels: [],\n            loaded: false,\n            chartOptions: {\n                chart: {\n                    type: 'line',\n                    height: 350,\n                },\n                stroke: {\n                    width: 5,\n                    curve: 'smooth',\n                },\n                markers: {\n                    size: 4,\n                    hover: {\n                        size: 6,\n                    },\n                },\n                xaxis: {\n                    categories: [],\n                    title: {\n                        text: 'Date',\n                    },\n                },\n                yaxis: {\n                    title: {\n                        text: 'Transactions',\n                    },\n                    labels: {\n                        formatter: function (val) {\n                            return val + 'k';\n                        },\n                    },\n                },\n                grid: {\n                    borderColor: '#e7e7e7',\n                    strokeDashArray: 5,\n                },\n                dataLabels: {\n                    enabled: false,\n                },\n                tooltip: {\n                    y: {\n                        formatter: function (val) {\n                            return val + ' transactions'\n                        },\n                    },\n                },\n            },\n        };\n    },\n    methods: {\n        async fetchTransactionData() {\n            try {\n                const response = await axios.get(`${this.page_info?.domain}teller-dashboard/weekly-transanctions`);\n                const data = response.data;\n                this.updateChart(data);\n                this.loaded = true;\n            } catch (error) {\n                console.error('Error fetching transaction data:', error);\n            }\n        },\n        updateChart(data) {\n\n            this.xAxisLabels = data.categories;\n            this.series = data.series.map(serviceType => ({\n                name: serviceType.name,\n                data: serviceType.data.map(amount => amount / 1000),\n            }));\n\n            this.chartOptions.xaxis.categories = this.xAxisLabels;\n        },\n    },\n    mounted() {\n        // Fetch the data when the component is mounted\n        this.fetchTransactionData();\n    },\n};\n\n</script>\n<style scoped>\n.loader {\n  border: 8px solid #f3f3f3; /* Light grey */\n  border-top: 8px solid #3498db; /* Blue */\n  border-radius: 50%;\n  width: 60px;\n  height: 60px;\n  animation: spin 1s linear infinite;\n}\n\n@keyframes spin {\n  0% { transform: rotate(0deg); }\n  100% { transform: rotate(360deg); }\n}\n\n</style>\n"],"sourceRoot":""}]);
