___CSS_LOADER_EXPORT___.push([module.id, "\n.loader[data-v-e241d7a6] {\n    border: 8px solid #f3f3f3;\n    border-top: 8px solid #3498db;\n    border-radius: 50%;\n    width: 60px;\n    height: 60px;\n    animation: spin-e241d7a6 1s linear infinite;\n}\n@keyframes spin-e241d7a6 {\n0% {\n        transform: rotate(0deg);\n}\n100% {\n        transform: rotate(360deg);\n}\n}\n", "",{"version":3,"sources":["webpack://./resources/js/components/pages/adminDashboard/charts/TransactionsStatus.vue"],"names":[],"mappings":";AA2FA;IACA,yBAAA;IACA,6BAAA;IACA,kBAAA;IACA,WAAA;IACA,YAAA;IACA,2CAAA;AACA;AAEA;AACA;QACA,uBAAA;AACA;AAEA;QACA,yBAAA;AACA;AACA","sourcesContent":["<template>\n    <div>\n        <div v-if=\"loaded\" class=\"d-lg-flex align-items-center gap-2\">\n\n            <apexchart type=\"donut\" height=\"350\" :options=\"chartOptions\" :series=\"series\"></apexchart>\n\n            <div>\n                <div>\n                    <span style=\"width: 5px;height: 5px;\" class=\"badge bg-warning me-1 rounded-circle\"></span>\n                    <span>\n                        <div>Failed Transaction</div>\n                        <div class=\"fs-24\">{{ series[1] }} K</div>\n                    </span>\n                </div>\n                <div>\n                    <span class=\"badge-success p-2 w-2 h-2\"></span>\n                    <span>\n                        <div>Successful Transaction</div>\n                        <div class=\"fs-24\">{{ series[0] }} K</div>\n                    </span>\n                </div>\n            </div>\n        </div>\n        <div v-else class=\"loader mx-auto\"></div>\n    </div>\n\n</template>\n\n<script>\nimport VueApexCharts from 'vue-apexcharts';\nimport axios from 'axios';\n\nexport default {\n    props:{\n        page_info:{\n            type: Object\n        }\n    },\n    components: {\n        apexchart: VueApexCharts,\n    },\n    data() {\n        return {\n            series: [],\n            loaded: false,\n            chartOptions: {\n                labels: ['Successful Transaction', 'Failed Transaction'],\n                chart: {\n                    type: 'donut',\n                },\n                stroke: {\n                    width: 2,\n                },\n                dataLabels: {\n                    enabled: true,\n                },\n                tooltip: {\n                    y: {\n                        formatter: function (val) {\n                            return val + 'k';\n                        },\n                    },\n                },\n                legend: {\n                    position: 'bottom',\n                },\n            },\n        };\n    },\n    methods: {\n        async fetchTransactionData() {\n            try {\n                const response = await axios.get(`${this.page_info?.domain}admin-dashboard/weekly-transactions`);\n                const data = response.data;\n                this.updateChart(data);\n                this.loaded = true;\n            } catch (error) {\n                console.error('Error fetching transaction data:', error);\n            }\n        },\n        updateChart(data) {\n            this.series = [(data.series.successful / 1000), (data.series.failed/1000)];\n        },\n    },\n    mounted() {\n        this.fetchTransactionData();\n    },\n};\n</script>\n\n<style scoped>\n.loader {\n    border: 8px solid #f3f3f3;\n    border-top: 8px solid #3498db;\n    border-radius: 50%;\n    width: 60px;\n    height: 60px;\n    animation: spin 1s linear infinite;\n}\n\n@keyframes spin {\n    0% {\n        transform: rotate(0deg);\n    }\n\n    100% {\n        transform: rotate(360deg);\n    }\n}\n</style>\n"],"sourceRoot":""}]);
